<template>
  <router-view></router-view>
</template>

<script>
import store from '../../store'

export default {
  name: 'UserNeeded',
  beforeRouteEnter (to, from, next) {
    if (store.getters.authToken) {
      next()
    } else {
      next({ name: 'Login' })
    }
  }
}
</script>

